<template>
<div>
  <van-grid :gutter="10" class="margin">
    <van-grid-item
      v-for="item in list"
      :key="item.name"
      :icon="item.icon"
      :text="item.text"
      :to="item.to"
      @click="gridClick(item)"
    />
  </van-grid>

</div>
</template>
<script>

import  {  menuData } from '@data'
export default {
  name: 'home',
  components: {
  },
  data(){
    return {
      list: menuData
    }
  },
  mounted() {


  },
  methods:{
    gridClick(item){
      if(item.to !== '/'){
        return false;
      }
      this.$toast(item.msg)
    }
  }
}
</script>
<style>
.margin {
  margin: 10px 0px;
}
